@import "vars";
@import "fonts";
@import "libs";
@import "_mixins/mix";
body {
  font-family: $default-font;
  overflow-x: hidden;
  background-color: #0a051a;
  color: #fff;
  font-size: 16px;
}
button:focus {
  outline: none;
}
h2 {
  font-weight: 550;
  font-size: 40px;
  line-height: 65px;
  margin-bottom: 80px;
  max-width: 935px;
  margin-left: auto;
  margin-right: auto;
}
a {
  &:hover {
    color: #00c2ff;
  }
}
h3 {
  font-size: 25px;
  line-height: 36px;
}
.grid {
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.4;
}
.g-btn {
  background-color: transparent;
  border: 0;
  cursor: pointer;
  color: #fff;
  display: inline-block;
  text-decoration: none;
  // line-height: 44px;

  &:hover {
    text-decoration: none;
    color: #fff !important;
  }

  &--blue {
    background: linear-gradient(100.33deg, #33bde9 4.41%, #004b75 103.21%);
    border-radius: 50px;
    padding: 10px 65px;
    transition: 0.3s;
    &:hover {
      background: linear-gradient(100.33deg, #33bde9 4.41%, #33bde9 103.21%);
    }
  }
  &--link {
    text-decoration: underline;
    padding: 0;
    &:hover {
      color: #00c2ff;
    }
  }
}
