//we
.we {
  position: relative;
  padding-bottom: 100px;
  .container {
    z-index: 2;
    position: relative;
  }
  .grid {
    left: 78%;
    z-index: 1;
  }
  &__galaxy {
    color: #0095ff;
    font-size: 25px;
    line-height: 65px;
    font-weight: normal;
  }
  h3 {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 70px;
  }
}

.we__circles {
  path {
    transition: 0.3s;
    cursor: pointer;
  }
  &--hover {
    path,
    circle {
      opacity: 0.2;
    }
    .advantages-list {
      li {
        opacity: 0.5;
        &::before {
          opacity: 0.3;
        }
      }
    }
  }
  .selected {
    path,
    circle {
      opacity: 1;
      filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    }
  }
}
.advantages-list {
  padding: 15px 0 15px 23px;
  display: flex;
  height: 100%;
  margin: 0;
  flex-direction: column;
  justify-content: space-between;
  font-size: 20px;
  line-height: 20px;
  list-style: none;

  li {
    position: relative;
    cursor: pointer;
    line-height: 50px;
    transition: all 0.3s;
    &.adv-list-selected {
      opacity: 1;
      position: relative;
      transform: scale(1.08);
      &::before {
        opacity: 1;
      }
    }

    &::before {
      content: "\2022";
      font-weight: bold;
      display: inline-block;
      width: 1em;
      position: absolute;
      top: 50%;
      left: -20px;
      transform: translateY(-50%);
    }
  }
}
.adv-list {
  &-1 {
    &::before {
      color: #a866fd;
    }
  }
  &-2 {
    &::before {
      color: #ff2466;
    }
  }
  &-3 {
    &::before {
      color: #f001d8;
    }
  }
  &-4 {
    &::before {
      color: #11178d;
    }
  }
  &-5 {
    &::before {
      color: #8000ff;
    }
  }
}
