//stages
.ball {
  width: 10px;
  height: 10px;
  background-color: red;
  border-radius: 50%;

  offset-path: path(
    "M3.008 2C3.008 2 544.975 2.00022 602.095 2C659.215 1.99978 706.927 40.6706 706.927 118.012C706.927 195.353 660.559 234.024 602.095 234.024C543.631 234.024 170.365 234.024 113.917 234.024C57.4686 234.024 2 272.694 2 350.036C2 427.377 58.1406 466.048 113.917 466.048C169.693 466.048 636.703 466.048 685.087 466.048"
  );
  offset-distance: 0%;

  animation: red-ball 2s linear alternate infinite;
}

@keyframes red-ball {
  from {
    offset-distance: 0%;
  }
  to {
    offset-distance: 100%;
  }
}

.stages-block-wrapp {
  min-height: 375px;
  position: relative;
  margin-top: 70px;
}
.stages {
  padding: 100px 0;
  position: relative;
  background: radial-gradient(
    1504.82px at 50% 50%,
    #2c60e730 0%,
    rgba(0, 0, 0, 0) 29%
  );
  .container {
    position: relative;
    z-index: 2;
  }
  h2 {
    margin-bottom: 30px;
  }
  &__block {
    background-color: #1c334d5e;
    padding: 50px 30px;
    border-radius: 10px;
    max-width: 295px;
    min-height: 375px;
    position: absolute;
    top: 0;
    h3 {
      margin-bottom: 30px;
      font-size: 21px;
    }
  }
  &__img {
    margin-top: 55px;
  }
  &__grid {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0.4;
  }
}

.stages-block {
  position: relative;
  margin-top: 100px;
}
.active-stage {
  .stage-item__num {
    top: -35px;
  }
}
.stage-item {
  cursor: pointer;
  position: absolute;
  transform: translate(calc(-50% + 2px), -50%);
  max-width: 175px;
  height: 50px;
  &:hover {
    transition: all 0.3s;
    &::after {
      width: 20px;
      height: 20px;
      box-shadow: 0px 0px 19px #4db5ff, 0px 0px 2px #63e7f9,
        0px 0px 19px #4db5ff, 0px 0px 5px #3eafff, 0px 0px 11px #4db5ff;
    }
  }
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -7px);
    content: "";
    display: block;
    transition: all 0.2s;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #4cd3fe;
  }

  &__num,
  &__title {
    display: block;
    text-align: center;
    transition: all 0.3s;
  }
  &__num {
    font-weight: bold;
    font-size: 25px;
    position: relative;
    top: -26px;
  }
  &__title {
    font-size: 16px;
    bottom: -12px;
    position: relative;
  }
  &:hover {
  }
  &-0,
  &-5,
  &-6 {
    left: 15%;
  }
  &-1,
  &-4,
  &-7 {
    left: 46%;
  }
  &-2,
  &-3,
  &-8 {
    left: 78%;
  }

  &-5,
  &-3,
  &-4 {
    top: 49.4%;
  }
  &-6,
  &-7,
  &-8 {
    top: 99%;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
  opacity: 1;
}
.slide-fade-leave-active {
  opacity: 1;
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active до версии 2.1.8 */ {
  transform: translateY(20px) scale(0.8);

  opacity: 0;
}

