//form
.input-group {
  margin-bottom: 26px;
}
.text-input {
  border-radius: 30px;
  border: 0;
  box-shadow: none;
  line-height: 40px;
  padding: 0 15px;
  font-size: 12px;
  color: #979797;
  box-sizing: border-box;
}

.confi-agreement {
  line-height: 14px;
  font-size: 10px;
  margin-bottom: 35px;
  &.error {
    color: #f35672;
    border: 1px solid #f35672;
    margin: -10px -10px 10px -10px;
    padding: 10px;
  }
  label {
    position: relative;
    text-align: left;
    box-sizing: border-box;
    cursor: pointer;
    user-select: none;
    span {
      padding-left: 30px;
      display: inline-block;
    }
  }
  .g-checkbox {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 12px;
    height: 12px;
    border: 1px solid #4db5ff;
    border-radius: 2px;
    position: absolute;
  }
}
.agreement {
  position: absolute;
  visibility: hidden;
  left: -20px;
  &:checked {
    & + label {
      .g-checkbox {
        border-color: #f35687;
        background-color: #f35687;
        &::after {
          content: "";
          display: block;
          height: 8px;
          width: 9px;
          background-size: contain;
          background-image: url("../img/check.svg");
        }
      }
    }
  }
}

